import React, { Component } from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';

import { articlesLoaded } from '../../redux/actions/actionCreators';
import {
  makeHeadersForAuth,
  loadAllArticles,
  loadUserArticles,
  loadAllArticlesWithOffset,
  loadUserlArticlesWithOffset,
} from '../../utils/utils';

import Preview from '../article/preview.jsx';
import Pagination from '@material-ui/lab/Pagination';

class ListArticles extends Component {
  getListArticles = async () => {
    const { showMode, showQuantity, setArticlesToState, isAuthorized } = this.props;
    const authHeaders = isAuthorized ? makeHeadersForAuth() : null;

    if (showMode === '') {
      const response = await loadAllArticles(showQuantity, authHeaders);
      const { articles, articlesCount } = await response.data;
      setArticlesToState(articles, articlesCount);
    } else {
      const response = await loadUserArticles(showQuantity, showMode, authHeaders);
      const { articles, articlesCount } = await response.data;
      setArticlesToState(articles, articlesCount);
    }
  };

  openArticle = (event, slug) => {
    const { history } = this.props;
    const isBtnParentClassList = event.target.parentElement.classList.contains('btnLike');
    if (!isBtnParentClassList) {
      history.push(`/blog-platform/articles/${slug}`);
    }
  };

  handleChangePagination = async (event) => {
    const { showMode, setArticlesToState, isAuthorized, showQuantity } = this.props;
    const pageNumber = event.currentTarget.textContent;
    const username = showMode;
    const offset = pageNumber > 1 ? pageNumber * 9 - 9 : null;
    const authHeaders = isAuthorized ? makeHeadersForAuth() : null;

    if (showMode === '') {
      const response = await loadAllArticlesWithOffset(showQuantity, offset, authHeaders);
      const { articles, articlesCount } = await response.data;
      setArticlesToState(articles, articlesCount);
    } else {
      const response = await loadUserlArticlesWithOffset(
        showQuantity,
        username,
        offset,
        authHeaders
      );
      const { articles, articlesCount } = await response.data;
      setArticlesToState(articles, articlesCount);
    }
  };

  componentDidMount() {
    this.getListArticles();
  }

  render() {
    const { listArticles, articlesCount, currentPage } = this.props;
    const mapedListArticles = listArticles.map((item) => {
      const { slug } = item;
      return (
        <div key={slug} onClick={(event) => this.openArticle(event, slug)}>
          <CardDiv>
            <Preview slug={slug} />
          </CardDiv>
        </div>
      );
    });

    const countPaginationBtns = Math.ceil(articlesCount / 9);
    const propsPagination = {
      count: countPaginationBtns,
      variant: 'outlined',
      shape: 'rounded',
      onChange: this.handleChangePagination,
      defaultPage: currentPage,
      articles: listArticles,
    };

    return (
      <ContainerDiv>
        <ListArticlesWrapDiv>{mapedListArticles}</ListArticlesWrapDiv>
        <PaginationDiv>
          <Pagination {...propsPagination} />
        </PaginationDiv>
      </ContainerDiv>
    );
  }
}

function mapStateToProps(state) {
  const { listArticles, articlesCount, isAuthorized, showMode, showQuantity, currentPage } = state;
  return {
    listArticles,
    articlesCount,
    isAuthorized,
    showMode,
    showQuantity,
    currentPage,
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setArticlesToState: (listArticles, articlesCount) =>
      dispatch(articlesLoaded(listArticles, articlesCount)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ListArticles);

const ContainerDiv = styled.div`
  display: flex;
  flex-direction: column;
`;

const ListArticlesWrapDiv = styled.div`
  display: flex;
  min-width: 840px;
  max-width: 1100px;
  flex-wrap: wrap;
  flex-grow: 1;
  margin-bottom: 15px;
  align-items: flex-start;
  align-content: flex-start;
  justify-content: flex-start;
`;

const CardDiv = styled.div`
  border: 1px solid gray;
  border-radius: 10px;
  box-shadow: 0 0 6px 0 #34495e;
  cursor: pointer;
  width: 250px;
  height: 280px;
  margin: 15px;
  margin-top: 5px;
  transition: 0.1s;
  &:hover {
    transform: scale(1.03);
  }
  display: flex;
  flex-direction: column;
`;

const PaginationDiv = styled.div`
  margin-left: auto;
  margin-right: auto;
  max-width: 500px;
  flex-grow: 0;
`;
